@import 'components/variables';
$size-circle-rotate-1200: 570px;
$size-circle-info-1200: calc(570px - 3rem);
$size-desktop-1200: calc(570px + 500px);

.section-empresas {
  padding-top: 100px;
  padding-bottom: 3rem;
  background: #efeff7;
  background-image: url(../images/empresas/bg-empresas-mb.png);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  @media (min-width: 1200px) {
    padding-top: 100px;
    background-image: url(../images/empresas/bg-empresas.png);
    background-size: 100% auto;
  }
  .container {
    p {
      color: $white;
      text-align: center;
    }
    .desktop {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      height: $size-desktop-1200;
      .circle-rotate {
        width: $size-circle-rotate-1200;
        height: $size-circle-rotate-1200;
        background-color: $red;
        border-radius: 50%;
        position: absolute;
        z-index: 1;
        .content-img {
          position: absolute;
          width: 100px;
          height: 100px;
          border-radius: 50%;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) rotate(-90deg);
          pointer-events: none;
        }
        #triangulo {
          width: 0;
          height: 0;
          border-left: 100px solid $red;
          border-top: 50px solid transparent;
          border-bottom: 50px solid transparent;
          transition: all 0.5s;

          &.pos-1 {
            transform: rotate(320deg) translate(325px);
          }

          &.pos-2 {
            transform: rotate(295deg) translate(325px);
          }

          &.pos-3 {
            transform: rotate(270deg) translate(325px);
          }

          &.pos-4 {
            transform: rotate(243deg) translate(325px);
          }

          &.pos-5 {
            transform: rotate(218deg) translate(325px);
          }

          &.pos-6 {
            transform: rotate(193deg) translate(325px);
          }

          &.pos-7 {
            transform: rotate(167deg) translate(325px);
          }

          &.pos-8 {
            transform: rotate(142deg) translate(325px);
          }

          &.pos-9 {
            transform: rotate(117deg) translate(325px);
          }

          &.pos-10 {
            transform: rotate(90deg) translate(325px);
          }

          &.pos-11 {
            transform: rotate(65deg) translate(325px);
          }

          &.pos-12 {
            transform: rotate(40deg) translate(325px);
          }
        }
      }
      .circle-info {
        width: $size-circle-info-1200;
        height: $size-circle-info-1200;
        background-color: $red2;
        border-radius: 50%;
        padding: 2rem;
        z-index: 2;
        .content-info {
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          .info {
            text-align: center;
          }
        }
        .content-img {
          position: absolute;
          width: 120px;
          height: 120px;
          border-radius: 50%;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          pointer-events: none;
        }
        .img-empresa {
          pointer-events: initial;
          cursor: pointer;
          width: 120px;
          height: 120px;
          border-radius: 50%;
          box-shadow: 0 0.5rem 1rem .25rem rgba($color: $text-gray, $alpha: 0.5);
          top: 50%;
          left: 50%;
          transition: all 0.3s;
          &:hover {
            box-shadow: 0 0.5rem 1rem .25rem rgba($color: $black, $alpha: 0.8);
            width: 130px;
            height: 130px;
          }
          &.img-1 {
            transform: rotate(231.4deg) translate(470px) rotate(-231.4deg);
          }
          &.img-2 {
            transform: rotate(205.7deg) translate(470px) rotate(-205.7deg);
          }
          &.img-3 {
            transform: rotate(180deg) translate(470px) rotate(-180deg);
          }
          &.img-4 {
            transform: rotate(154.3deg) translate(470px) rotate(-154.3deg);
          }
          &.img-5 {
            transform: rotate(128.6deg) translate(470px) rotate(-128.6deg);
          }
          &.img-6 {
            transform: rotate(102.9deg) translate(470px) rotate(-102.9deg);
          }
          &.img-7 {
            transform: rotate(77.1deg) translate(470px) rotate(-77.1deg);
          }
          &.img-8 {
            transform: rotate(51.4deg) translate(470px) rotate(-51.4deg);
          }
          &.img-9 {
            transform: rotate(25.7deg) translate(470px) rotate(-25.7deg);
          }
          &.img-10 {
            transform: rotate(0deg) translate(470px) rotate(-0deg);
          }
          &.img-11 {
            transform: rotate(334.3deg) translate(470px) rotate(-334.3deg);
          }
          &.img-12 {
            transform: rotate(308.6deg) translate(470px) rotate(-308.6deg);
          }
        }
      }
    }
  }
  .modal {
    .modal-dialog {
      .modal-content {
        background-color: $red;
        .modal-header {
          border: none;
          .btn-close {
            background: transparent url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath d='M.293.293a1 1 0 011.414 0L8 6.586 14.293.293a1 1 0 111.414 1.414L9.414 8l6.293 6.293a1 1 0 01-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 01-1.414-1.414L6.586 8 .293 1.707a1 1 0 010-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat;
            opacity: 1;
          }
        }
        .modal-body {
          padding: 2rem;
          display: flex;
          align-items: center;
          text-align: center;
          p {
            color: $white;
          }
        }
      }
    }
  }
}